import React from 'react'

const Members = (props) => {
  const {dynamicData} = props

  return (
    <div className="content-container">
      <div className="members">
        <div className='shade1'/>
          <div className="members-container">
          <div className="members__title">
            <h2>Ativos</h2>
          </div>
            {
              dynamicData.members.filter(item => item.active ==='active').sort((a,b) => (a.name > b.name) ? 1 : ((b.name > a.name) ? -1 : 0)).map((item, i) => (
                  <div>
                    <img src={item.imgUrl} className="members__image" />
                    <h2>{item.name}</h2>
                    <p className="members__text" >{item.text}</p>
                  </div>
                )
              )
            }
            <div className="members__title">
              <h2>Inativos</h2>
            </div>
            {
              dynamicData.members.filter(item => item.active ==='inactive').sort((a,b) => (a.name > b.name) ? 1 : ((b.name > a.name) ? -1 : 0)).map((item, i) => (
                  <div>
                    <img src={item.imgUrl} className="members__image"/>
                    <h2>{item.name}</h2>
                    <p className="members__text" >{item.text}</p>
                  </div>
                ))
            }
        <div className='shade2' style={{bottom: '-1px'}}/>
      </div>
      </div>
    </div>
  )
}

export default Members;
