import React from 'react'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

const GalleryEdit = (props) => {
  const {
    handleGalleryModalOpen,
    handleGallerySubmit,
    handleGalleryChange,
    handleGalleryEditClick,
    handleGalleryUpload,
    handleGalleryDelete,
    handleCategoryChange,

    isGalleryModalOpen,
    gallery,
    currentImage
  } = props

  return (
    <>
      <div className="content-container">
        <div className="admin-gallery">
          <div className="admin-gallery-container">
            {gallery.map((item, i) => {
              return(
                <>
                  <div key = {item.id} className="gallery-card">
                    <div className="gallery-card__image gallery-card__background" style={{backgroundImage: `url(${item.imageUrl})`}}>
                    </div>
                    <h1 style={{color: '#ffa702'}}>{item.category}</h1>
                    <button style={{color: 'red'}} onClick={() => handleGalleryDelete(item.id, item.imageUrl)}><span role="img" aria-label="out">❌</span></button>
                  </div>

                </>
              )
            })}
          </div>
          <div className="admin-gallery-add">
            <div className="admin-gallery-add__category">
              <label className="admin-gallery-add__label">Categoria: </label>
              <select name="categories" id="categories" onChange={e => handleCategoryChange(e)}>
                <option>Escolha uma categoria</option>
                <option value="meets">Encontros</option>
                <option value="concerts">Espetaculos</option>
                <option value="studio">estudio</option>
                <option value="coimbra">Guitarra de Coimbra</option>
                <option value="ualg">Serenatas ualg</option>
              </select>
            </div>
              <div>
                <label className="admin-gallery-add__label">Descrição: </label>
                <input type="text" name="description" className="admin-gallery-add__category-input"  onChange={e => handleGalleryUpload(e)}/>
              </div>
              <input type="file" name="file"  onChange={e => handleGalleryUpload(e)}/>
              <button
                className = 'shared-button shared-button--second'
                onClick = {handleGallerySubmit}
                type='submit'
                title='submit'
              >
                <FontAwesomeIcon icon={['fas','plus']} />
              </button>
          </div>
        </div>
      </div>
    </>
  )
}

export default GalleryEdit
