import React from 'react'

const Gallery = (props) => {
  const {
    galleryCategories,
    currentGallery,
    handleGallerySelect,
    resetGallery
  } = props

  return(

    <div className="content-container">
      {currentGallery.length === 0 ? (
      <>
        <div className="gallery">
        <div className="shade1"/>

          <div className="gallery__list">

            {galleryCategories.map(item => (
              <div className="gallery__row" onClick = {() => handleGallerySelect(item.to)}>
                <h1>{item.label}</h1>
              </div>
            ))}
          </div>
        </div>
        </>
      ) : (
        <>
          <div>
            <div className="shade1"/>
            {/* <label className="back" onClick={resetGallery} style={{color: 'white'}}>back</label> */}
            {currentGallery.map(item => (
              <>
                <img src={item['imageUrl']} className="gallery__image" alt="home"/>
                <p style={{color: 'white', textAlign: 'center', fontSize: '1rem'}}>{item.description}</p>
              </>
            ))}
          </div>
          <div className="shade2"/>
        </>
      )}

    </div>
  )
}

export default Gallery